import React, { Component } from 'react';
import './App.css';
import routes from './routes';

class App extends Component {
  render() {
    return (
      <div className="App">
          {routes}
      </div>
    );
  }
}

export default App;


// component-based architecture is a react pattern, in which different of the app's views are coded separately in exportable/importable blocks that may be connected later.
// unidirectional data flow refers to a global coding pattern that is essential in react. having state that originates in a parent component, designed to pass it down (as props) to each child component
// that will be rendering it. react is designed for strict downward flow of data in this way.
// the virtual DOM is react's middleman to the actual browser DOM. changes in data/state are handled in react's render method before being sent to the actual DOM, making state changes much faster to appear
